.btns-links {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    max-width: 500px;
    margin: auto;
}

.btns-links button {
    border: none;
    border-radius: 3px;
    padding: 10px 25px;
    width: 200px;
    margin: 0 0 10px;
}

.btns-links button .fa-share {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
    margin: 0 10px 0 0;
    font-size: 18px;
    vertical-align: top;
}

.responsives {
    margin: 15px 0;
}

@media screen and (max-width: 799px) {
    .imgDevices {
        width: 100%;
        max-width: 290px;
        margin: auto;
        max-height: inherit;
        position: relative;
        right: 0;
    }
    .imgDevices img {
        width:100%;
    }
}


